import { Cloud } from 'lucide-react'
import Link from 'next/link'
import React from 'react'

export default function Header() {
  return (
    <header className=' sticky top-0 w-full px-4 py-2'>
        <div className='max-w-7xl mx-auto flex justify-between items-center'>
            {/* Logo */}
            <Link href="/" className=''>
                <h1 className='text-3xl font-medium flex items-center gap-2'><span><Cloud size={34} className='text-primary'/></span>Cloud for Nothing</h1>
            </Link>

            {/* Desktop Nav */}
            <nav className='hidden md:flex space-x-4'>
                <Link href="/" className='hover:text-primary transition'>About</Link>
                <Link href="/about" className='hover:text-primary transition'>Features</Link>
                <Link href="/contact" className='hover:text-primary transition'>Support</Link>
            </nav>

            {/* Mobile nav */}
            <div className='block md:hidden'>
                {/* Mobile menu button */}
                <button className='p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-inset focus:ring-primary'>
                    <span className='sr-only'>Open main menu</span>
                    {/* Icon when menu is closed */}
                    <svg className='h-6 w-6' xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" aria-hidden="true">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
                    </svg>
                </button>
            </div>

            <nav className='md:hidden'>
                
            </nav>
        </div>
    </header>
  )
}
